  font-display: swap;
  src: url("../fonts/Unbounded-Regular.eot") format("eot");
  src: url("../fonts/Unbounded-Regular.woff") format("woff"), url("../fonts/Unbounded-Regular.woff2") format("woff2"), url("../fonts/Unbounded-Regular.svg") format("svg"), url("../fonts/Unbounded-Regular.ttf") format("ttf");
