  font-family: "Unbounded";
  font-display: swap;
  src: url("../fonts/Unbounded-Bold.eot") format("eot");
  src: url("../fonts/Unbounded-Bold.woff") format("woff"), url("../fonts/Unbounded-Bold.woff2") format("woff2"), url("../fonts/Unbounded-Bold.svg") format("svg"), url("../fonts/Unbounded-Bold.ttf") format("ttf");
  font-weight: 700;
  font-style: normal;
}
/**
 * Functions for ViewPort Calculating
============
  SCSS
  .selector {
    font-size: px(18);
    @include md
      font-size: pxm(14);
    @include xs
      font-size: pxs(12);
  }

  CSS RESULT
  .selector {
    font-size: 18px;
  }
  @media (max-width: 768px) {
    .selector {
      font-size: 14px;
    }
  }
  @media (max-width: 320px) {
    .selector {
      font-size: 12px;
    }
  }
============
*/
/**
  function VA => use for calculate letter-spacing from photoshop
============
  SCSS
  .selector {
    letter-spacing: VA(120, 14);
  }

  CSS RESULT
  .selector {
    letter-spacing: 1.68px;
  }
============
  $unit => VA value in photoshop
  $font_size => how px font-size in photoshop ||| default 16
  $return px | em ||| default px
 */
:root {
  --baseWhite: #fff;
  --baseBlack: #000;
  --baseText: #000;
  --header-height: 51px;
  --scrollbar-current-width-y: 0px;
  --scrollbar-static-width-y: 0px;
}

html,
body {
  scroll-behavior: smooth;
}
@media (max-width: 992px) {
  html.scroll-locked,
  body.scroll-locked {
    overflow: hidden;
  }
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: black;
  font-weight: normal;
  line-height: 1.3;
  font-size: 20px;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  padding-right: var(--scrollbar-current-width-y);
}

h1,
