}
.select.active .select__trigger .icon-arrow-select {
  transform: rotate(-180deg);
}
.select.active .select__dropdown {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}
.select__trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 5px 15px;
  text-align: start;
  position: relative;
  z-index: 3;
  border: 1px solid #000;
  background-color: var(--baseWhite);
  cursor: pointer;
  transition: all 0.3s ease;
}
.select__trigger:focus-within {
  box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.2);
}
.select__trigger-value {
  display: block;
  width: 100%;
  -webkit-line-clamp: 1;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.select__trigger .icon {
  margin-left: 10px;
}
.select__dropdown {
  width: 100%;
  height: 150px;
  position: absolute;
  top: calc(100% + 10px);
  left: 0;
  z-index: 2;
  background-color: var(--baseWhite);
  border: 1px solid #000;
  transition: all 0.3s ease;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-30px);
}
.select__list {
  max-height: 100%;
  overflow-y: auto;
}
.select__link, .select__btn {
