.icon-menu {
  display: block;
  position: relative;
  width: 43px;
  height: 32px;
  cursor: pointer;
  z-index: 2000;
}
@media (min-width: 991px) {
  .icon-menu:hover span, .icon-menu:hover::before, .icon-menu:hover::after {
    background-color: #72c2ff;
  }
}
.icon-menu span, .icon-menu::before, .icon-menu::after {
  content: "";
  transition: all 300ms ease 0ms;
  right: 0;
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: #fff;
}
.icon-menu::before {
  top: 1px;
  width: 100%;
}
.icon-menu::after {
  bottom: 0;
  width: 100%;
}
.icon-menu span {
  top: calc(50% - 1px);
}
.icon-menu._active::before, .icon-menu._active::after {
  opacity: 0;
}
.icon-menu._active span {
  transform: scale(0);
}
