/* Стили для чекбокса и его галочки */
input[type=checkbox] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 18px;
  height: 18px;
  border: 1px solid rgb(206, 206, 206);
  outline: none;
  cursor: pointer;
  position: relative;
}

input[type=checkbox]::after {
  content: "✓"; /* Юникод символ галочки */
  position: absolute;
  top: -1px;
  left: 3px;
  font-size: 14px;
  color: #007bff;
  visibility: hidden;
}

/* При нажатии на чекбокс показываем галочку */
input[type=checkbox]:checked::after {
  visibility: visible;
}

