.icon-menu {
  display: block;
  position: relative;
  width: 43px;
  height: 32px;
  cursor: pointer;
  z-index: 2000;
  @media (min-width: 991px) {
    &:hover {
      span,
      &::before,
      &::after {
        background-color: #72c2ff;
      }
    }
  }

  span,
  &::before,
  &::after {
    content: '';
    transition: all 300ms ease 0ms;
    right: 0;
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #fff;
  }
  &::before {
    top: 1px;
    width: 100%;
  }
  &::after {
    bottom: 0;
    width: 100%;
  }
  span {
    top: calc(50% - 1px);
  }
  &._active {
    &::before,
    &::after {
      opacity: 0;
    }
    span {
      transform: scale(0);

      &:first-child {
        transform: rotate(-45deg);
        top: calc(50% - 3px);
      }

      &:last-child {
        transform: rotate(45deg);
        top: calc(50% - 3px);
      }
    }
  }
}
